}

.links__container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.links__header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.links__title {
  .font(short; 22px; 28px; 800);
  color: #1D255A;
}

.links__actions {

}

.links__message {
  display: none;
  width: 100%;
  .font(short; 18px; 28px; 600);
  color: #D8D8D8;

  &--active {
    display: block;
  }
}

.links__btn {
  padding: 5px;

  &--question {
    margin-right: 22px;
  }
}

.links__open {
  position: absolute;
  right: 0;
  bottom: 15px;
  padding: 0;
  
  .links__btn-icon {
    fill: #D8D8D8;
    transition: 0.5s ease;
    transform: rotate(-90deg);
  }

  &--active {
    .links__btn-icon {
      transform: rotate(90deg);
    }
  }
}

.links__btn-icon {

}

.links__items {
  margin-bottom: 40px;
}

.links__item {
  padding: 15px 0;
  border-bottom: 1px solid #E0E0E0;
}

.links__block {
  display: flex;

  &--outer {
    position: relative;
    padding-bottom: 15px;
    padding-right: 25px;
  }
}

.links__item-column {
  &--left {
    margin-right: 16px;
  }

  &--right {
    flex-grow: 1;
  }
}

.links__item-media {
  // width: 44px;
  // height: 44px;
  // padding: 10px 6px;
  // border: 1px solid #D8D8D8;
  // border-radius: 8px;

  width: 48px;
  height: 48px;
  border-radius: 50%;
  border-radius: 12px;

  .links__item-img {
    border-radius: 50%;
    border-radius: 12px;
  }
}

.links__item-ad {
  display: none;
}

.links__visit--ad {
  .links__item-media {
    // width: 44px;
    // height: 44px;
    padding: 10px 6px;
    border: 1px solid #D8D8D8;
    border-radius: 8px;
  
    width: 44px;
    height: 44px;

    .links__item-ad {
      display: inline-block;
    }

    .links__item-img {
      display: none;
    }
  }
}

.links__item-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.links__item-title {
  .font(short; 14px; 18px; 600);
  color: #1D255A;
  &--small {
    .font(short; 10px; 14px; 400);
  }
}

.links__item-date {
  .font(shortest; 12px; 15px);
  color: #333333;
  opacity: 0.6;
  
  &--time {
    .font(short; 12px; 12px; 700);
    color: #1D255A;
    opacity: 1;

    .svg-icon {
      // fill: red;
      margin-right: 6px;
      line-height: 0;
      vertical-align: bottom;
    }
  }
}

.links__item-info {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.links__item-last {
  .font(shortest; 12px; 18px);
  color: #8A8E96;
}

.links__item-badge {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 3px 10px;
  background: #585DF9;
  border-radius: 12px;
  .font(short; 12px; 17px; 700);

  .links__item-icon {
    margin-right: 4px;
    fill: #fff;
  }
}

.links__visits {
  // max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease;
  height: 0;

  &--active {
  }
}

.links__inner {
  

}

.links__visit {
  margin-bottom: 10px;
}

.links__visits-date {
  margin-bottom: 4px;
  .font(short; 10px; 20px; 700);
  color: #1D255A;

  .links__item-icon {
    margin-right: 5px;
    vertical-align: middle;
    fill: #7C80FC;
  }
}

.links__visits-items {

}

.links__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
  flex-wrap: wrap;
  flex-direction: column;
}

.links__more {
  margin-bottom: 50px;
  max-width: 315px;
  width: 100%;
  padding: 15px;
}

.links__badge {
  width: 100%;
  max-width: 315px;
}

.links__advertisement {
  display: none;
}

.links__visit--ad {
  .links__item-date--time {
    color: #585DF9;

    .links__item-icon {
      fill: #585DF9;
    }
  }
}

.links--empty {
  .links__message {
    display: block;
  }

  .links__advertisement {
    display: block;
  }
}

.links--details {
  position: static;
  background: none;
  min-height: auto;
  border-radius: 0;
  display: flex;
  justify-content: center;
  padding: 0;

  &::before,
  &::after {
    content: none;
  }

  .links__item-title {
    margin-right: 0;
  }

  .links__item-column--left {
    margin-right: 0;
  }

  .links__item-last {
    max-width: 50%;
  }

  .links__block--outer  {
    padding-right: 0;
  }

  .links__item {
    padding: 0;
    border: none;
  }

  .links__item-last {
    color: #fff;
  }

  .links__header {
    display: none;
  }

  .links__item-header--desktop {
    display: none;
  }
}

@media @tablet-width {
  .links {
    position: relative;
    background: none;

    &::before,
    &::after {
      content: none;
    }
  }

  .links__title {
    flex-grow: 1;
    .font(short; 32px; 50px; 800);
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
  }

  .links__actions {
    display: none;
  }

  .links__message {
    display: none;
  }

  .links__item-title {
    .font(shortest; 18px; 23px);
    color: #FFFFFF;

    &--small {
      .font(shortest; 16px; 18px);
    }
  }

  .links__item-column {
    &--left {
      margin-right: 16px;
    }
  
    &--right {
      flex-grow: 1;
    }

    &--desktop {

      .links__container {
      }


      .links__item-header {
        padding-top: 15px;
        flex-wrap: wrap;
      }

      .links__item-fulldate {
        width: 100%;
        margin-bottom: 11px;
        .font(short; 14px; 18px; 600);
        color: #7C80FC;

        .links__item-icon {
          margin-right: 5px;
          fill: #7C80FC;
        }
      }

      .links__item-badge {
        .font(short; 16px; 20px; 700);
        background: #fff;
        color: #585DF9;

        .links__item-icon {
          fill: #585DF9;
        }
      }

      .links__item-count {
        margin-right: 15px;
        .font(short; 18px; 18px; 700);
        color: #F2F2F2;
      }
    }
  }

  .links__item--desktop {
    padding: 40px 0 30px 0;
    border-color: #5A5DAA;

    .links__inner {
      padding-left: 161px;
    }

    .links__open {
      position: static;
      margin-left: 15px;
      // .align(vertical);

      .links__btn-icon {
        fill: #8286FD;
      }
    }

    .links__visits-date {
      margin-bottom: 15px;
      .font(short; 14px; 18px; 600);
      color: #7C80FC;
    }

    .links__block--outer {
      .links__item-media {
        width: 130px;
        height: 105px;
        // padding: 10px 6px;
        // border: 1px solid #D8D8D8;
        border-radius: 12px;
      }

      .links__item-column {
        &--left {
          margin-right: 30px;
        }
      }
    }

    .links__block--inner {
      .links__item-media {
        display: flex;
        justify-content: center;
        align-items: center;
        // margin-left: 30px;
        width: 64px;
        height: 64px;
        height: auto
      }
    }

    .links__item-date {
      &--time {
        .font(short; 16px; 16px; 700);
        color: #FFFFFF;

        .links__item-icon--clock {
          margin-right: 6px;
          fill: #fff;
          vertical-align: baseline;
        }
      }
    }

    .links__item-last {
      font-size: 16px;
      line-height: 18px;
      color: #B9B9B9;
    }
  }

  .links__visit--ad {
    .links__item-date--time {
      color: #585DF9;
  
      .links__item-icon {
        fill: #585DF9;
      }
    }
  }

  .links--details {
    .links__header {
      display: block;
    }

    .links__item-header--desktop {
      display: flex;
    }


  }
}